export default {
	name: 'traOrder',
	data() {
		return {
			modalName: this.setModalName,
			phone: this.setPhone,
		}
	},
	props: {
		modalName:{
			type: String,
			default:""
		},
		phone:{
			type: String,
			default:""
		}
	},
	methods: {
		hideModal(){
			this.$emit("hideModal",this.phone)
		},
		trchangOrder(e){
			this.$emit("trchangOrder",this.modalName)
		}
